import {
  View,
  Text,
  TouchableOpacity,
  StyleSheet,
  Platform,
} from "react-native";
import DateTimePicker, {
  DateTimePickerAndroid,
} from "@react-native-community/datetimepicker";
import { useState } from "react";

type IDatePicker = {
  label: string;
  dateValue: string;
  onDateChange: (date: any) => void;
};

const DatePicker = (props: IDatePicker) => {
  const { label, dateValue, onDateChange } = props;
  const [showDatePicker, setShowDatePicker] = useState(false);

  const openDatePicker = () => {
    DateTimePickerAndroid.open({
      value: new Date(parseDateString(dateValue)),

      onChange(event, date) {
        setDate(date);
      },
      mode: "date",
      display: "spinner",
    });
  };

  const parseDateString = (dateString: string) => {
    const months = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];

    const [day, monthStr, year] = dateString?.split("-");
    const monthIndex = months.indexOf(monthStr);

    // Ensure the format is correct before creating the Date object
    if (day && monthIndex !== -1 && year) {
      return new Date(Number(year), monthIndex, Number(day));
    } else {
      console.error("Invalid date string format");
      return new Date(); // Return a default date if parsing fails
    }
  };

  function convertDateFormat(dateString: string) {
    const months = [
      "Jan",
      "Feb",
      "Mar",
      "Apr",
      "May",
      "Jun",
      "Jul",
      "Aug",
      "Sep",
      "Oct",
      "Nov",
      "Dec",
    ];

    // Create a Date object from the input string
    const inputDate = new Date(dateString);

    // Extract day, month, and year components
    const day = inputDate.getUTCDate();
    const monthIndex = inputDate.getUTCMonth();
    const year = inputDate.getUTCFullYear();

    // Format the date in the desired format
    const formattedDate = `${day}-${months[monthIndex]}-${year}`;

    return formattedDate;
  }

  const setDate = (date: any) => {
    console.log(convertDateFormat(date));
    onDateChange(convertDateFormat(date));
  };

  return (
    <View>
      <Text style={styles.label}>{label}</Text>
      <TouchableOpacity
        onPress={() => {
          Platform.OS == "android" ? openDatePicker() : setShowDatePicker(true);
        }}
        style={styles.dateInput}
      >
        <Text style={styles.dateText}>{dateValue}</Text>
      </TouchableOpacity>
      {showDatePicker && (
        <DateTimePicker
          value={parseDateString(dateValue)}
          mode="date"
          display="default"
          onChange={setDate}
        />
      )}
    </View>
  );
};

const styles = StyleSheet.create({
  label: {
    fontSize: 16,
    fontWeight: "500",
    marginBottom: 8,
  },
  dateInput: {
    borderWidth: 1,
    borderColor: "#ddd",
    borderRadius: 6,
    padding: 15,
    marginBottom: 20,
  },
  dateText: {
    fontSize: 16,
  },
});

export default DatePicker;
